import * as React from "react";
import { Link } from "gatsby";
import { BOOKS } from "@data";
import { OmegaLayout } from "@components/layouts";
import Book from "@components/common/Books/Book";
import SmallDescription from "@components/omega/SmallDescription";
import Tools from "../components/common/Tools/Tools";

const BooksPage = () => (
    <OmegaLayout>
        <SmallDescription
            title="Books"
            text={
                <>
                    This is a log of the books I've read.
                    <br />I have developed a habit of reading books. I had to
                    use a number of tricks to help me read more, stay focused
                    &amp; remember the most important lessons from a book.
                </>
            }
        />

        <div className="container my-5 pb-5">
            {BOOKS.filter((book) => book.read).map((book, index) => (
                <Book book={book} position={index} key={index} />
            ))}

            <div className="my-5 py-5 fs-1 text-center ">
                <b> To Be Read </b>
            </div>

            {BOOKS.filter((book) => !book.read).map((book, index) => (
                <Book book={book} position={index} key={index} />
            ))}
        </div>
    </OmegaLayout>
);

export default BooksPage;
